import React, { useState } from 'react';
import HelpModal from './HelpModal'; // Adjust path as per your project structure

const Navbar = () => {
  const [helpModalOpen, setHelpModalOpen] = useState(false);

  const openHelpModal = () => {
    setHelpModalOpen(true);
  };

  const closeHelpModal = () => {
    setHelpModalOpen(false);
  };

  return (
    <nav className="bg-blue-600 p-4 text-white">
      <div className="flex justify-between items-center">
        <div className="text-lg font-bold">Salesforce Event Monitoring - Login</div>
        <button onClick={openHelpModal} className="bg-blue-500 text-white px-3 py-1 rounded">
          Help
        </button>
      </div>
      <HelpModal isOpen={helpModalOpen} onClose={closeHelpModal} />
    </nav>
  );
};

export default Navbar;
