import React, { useEffect, useRef, useState } from 'react';
import * as d3 from 'd3';
import html2canvas from 'html2canvas';

function DataVisualization({ data }) {
  const [topN, setTopN] = useState(10);
  const [chartWidth, setChartWidth] = useState(1000);
  const [chartHeight, setChartHeight] = useState(500);
  const [showGrid, setShowGrid] = useState(false);
  const [chartRendered, setChartRendered] = useState(false);
  const [barColor, setBarColor] = useState('steelblue'); // Default color
  const [timeInterval, setTimeInterval] = useState('day'); // Default time interval

  
  const svgRef = useRef();
  const lineChartRef = useRef();

  // Function to render the bar chart
  const renderBarChart = () => {
    if (data.length === 0) return;

    // Group data by USER_NAME and count occurrences
    const userCounts = d3.rollup(data, v => v.length, d => d.USER_NAME);

    // Convert map to array and sort by count
    const sortedUserCounts = Array.from(userCounts, ([user, count]) => ({ user, count }))
      .sort((a, b) => d3.descending(a.count, b.count))
      .slice(0, topN);

    // Dynamically calculate left margin based on longest user name
    const maxLengthUserName = sortedUserCounts.reduce((max, d) => Math.max(max, d.user.length), 0);
    const margin = { top: 20, right: 80, bottom: 40, left: maxLengthUserName * 7 };

    const width = chartWidth - margin.left - margin.right;
    const height = chartHeight - margin.top - margin.bottom;

    // Remove any existing SVG
    d3.select(svgRef.current).selectAll('*').remove();

    const svg = d3.select(svgRef.current)
      .attr('width', width + margin.left + margin.right)
      .attr('height', height + margin.top + margin.bottom)
      .append('g')
      .attr('transform', `translate(${margin.left},${margin.top})`);

    // Set up scales
    const x = d3.scaleLinear()
      .domain([0, d3.max(sortedUserCounts, d => d.count)])
      .range([0, width]);

    const y = d3.scaleBand()
      .domain(sortedUserCounts.map(d => d.user))
      .range([0, height])
      .padding(0.1);

    // Add bars with rounded corners
    svg.selectAll('.bar')
      .data(sortedUserCounts)
      .enter().append('rect')
      .attr('class', 'bar')
      .attr('x', 0)
      .attr('y', d => y(d.user))
      .attr('width', d => x(d.count))
      .attr('height', y.bandwidth())
      .attr('fill', barColor)
      .attr('rx', 5) // Rounded corners
      .attr('ry', 5);

    // Add text labels on the right side of the bars
    svg.selectAll('.label')
      .data(sortedUserCounts)
      .enter().append('text')
      .attr('class', 'label')
      .attr('x', d => x(d.count) + 5) // Add a little padding to the right side of the bar
      .attr('y', d => y(d.user) + y.bandwidth() / 2)
      .attr('dy', '.35em')
      .attr('text-anchor', 'start')
      .attr('fill', 'black')
      .text(d => d.count);

    // Add x-axis
    svg.append('g')
      .attr('class', 'x-axis')
      .attr('transform', `translate(0,${height})`)
      .call(d3.axisBottom(x));

    // Add y-axis
    svg.append('g')
      .attr('class', 'y-axis')
      .call(d3.axisLeft(y));

    // Add grid lines
    if (showGrid) {
      svg.append('g')
        .attr('class', 'grid')
        .attr('transform', `translate(0,${height})`)
        .call(d3.axisBottom(x)
          .tickSize(-height)
          .tickFormat(''))
        .selectAll('.tick line')
        .attr('stroke', 'lightgrey');

      svg.append('g')
        .attr('class', 'grid')
        .call(d3.axisLeft(y)
          .tickSize(-width)
          .tickFormat(''))
        .selectAll('.tick line')
        .attr('stroke', 'lightgrey');
    }
  };

  // Function to render the line chart
  const renderLineChart = () => {
    if (data.length === 0) return;
  
    // Parse the dates and count the number of logins per day
    const loginsByDate = d3.rollup(
      data,
      v => v.length,
      d => d.TIMESTAMP ? d3.timeParse("%Y%m%d%H%M%S")(d.TIMESTAMP.slice(0, 14)) : null
    );
  
    // Remove entries with null dates
    const filteredLoginsByDate = Array.from(loginsByDate).filter(([date, count]) => date !== null);
  
    // Convert map to array and sort by date
    const sortedLoginsByDate = filteredLoginsByDate.map(([date, count]) => ({ date, count }))
      .sort((a, b) => d3.ascending(a.date, b.date));
  
    const margin = { top: 20, right: 30, bottom: 30, left: 50 };
    const width = chartWidth - margin.left - margin.right;
    const height = chartHeight - margin.top - margin.bottom;
  
    // Remove any existing SVG
    d3.select(lineChartRef.current).selectAll('*').remove();
  
    const svg = d3.select(lineChartRef.current)
      .attr('width', width + margin.left + margin.right)
      .attr('height', height + margin.top + margin.bottom)
      .append('g')
      .attr('transform', `translate(${margin.left},${margin.top})`);
  
    // Set up scales
    const x = d3.scaleTime()
      .domain(d3.extent(sortedLoginsByDate, d => d.date))
      .range([0, width]);
  
    const y = d3.scaleLinear()
      .domain([0, d3.max(sortedLoginsByDate, d => d.count)])
      .range([height, 0]);
  
    // Add the line
    const line = d3.line()
      .x(d => x(d.date))
      .y(d => y(d.count));
  
    svg.append('path')
      .datum(sortedLoginsByDate)
      .attr('class', 'line')
      .attr('fill', 'none')
      .attr('stroke', 'steelblue')
      .attr('stroke-width', 1.5)
      .attr('d', line);
  
    // Add x-axis
    svg.append('g')
      .attr('class', 'x-axis')
      .attr('transform', `translate(0,${height})`)
      .call(d3.axisBottom(x).ticks(d3.timeDay.every(2)).tickFormat(d3.timeFormat('%Y-%m-%d')));
  
    // Add y-axis
    svg.append('g')
      .attr('class', 'y-axis')
      .call(d3.axisLeft(y));
  
    // Add grid lines
    if (showGrid) {
      svg.append('g')
        .attr('class', 'grid')
        .attr('transform', `translate(0,${height})`)
        .call(d3.axisBottom(x)
          .tickSize(-height)
          .tickFormat(''))
        .selectAll('.tick line')
        .attr('stroke', 'lightgrey');
  
      svg.append('g')
        .attr('class', 'grid')
        .call(d3.axisLeft(y)
          .tickSize(-width)
          .tickFormat(''))
        .selectAll('.tick line')
        .attr('stroke', 'lightgrey');
    }
  };
   

  useEffect(() => {
    renderBarChart();
    renderLineChart();
    setChartRendered(true); // Set chart rendered after both charts have been rendered
  }, [data, topN, chartWidth, chartHeight, showGrid, barColor]);

  const wrapperRef = useRef();

  const downloadChartAsPng = () => {
    if (!svgRef.current) {
      console.error('SVG element is not ready.');
      return;
    }

    html2canvas(wrapperRef.current)
      .then((canvas) => {
        const link = document.createElement("a");
        link.href = canvas.toDataURL("image/png");
        link.download = "chart.png";
        link.click();
      })
      .catch((err) => console.error("Error capturing chart:", err));
  };

  return (
    <div className="my-4">
      {chartRendered &&
        <div className="mb-4 p-4 rounded-lg bg-blue-100">
          <label className="mr-2">Number of top users to display:</label>
          <input
            type="number"
            value={topN}
            onChange={e => setTopN(Number(e.target.value))}
            min="1"
            className="border p-1 mr-4"
          />
          <label className="mr-2">Chart Width:</label>
          <input
              type="number"
              value={chartWidth}
              onChange={e => setChartWidth(Number(e.target.value))}
              min="100"
              className="border p-1 mr-4"
            />
            <label className="mr-2">Chart Height:</label>
            <input
              type="number"
              value={chartHeight}
              onChange={e => setChartHeight(Number(e.target.value))}
              min="100"
              className="border p-1 mr-4"
            />
            <label className="mr-2">Show Grid:</label>
            <input
              type="checkbox"
              checked={showGrid}
              onChange={e => setShowGrid(e.target.checked)}
              className="border p-1 mr-2"
            />
            <label className="mr-2">Bar Color:</label>
            <input
              type="color"
              value={barColor}
              onChange={e => setBarColor(e.target.value)}
              className="border p-1"
            />
            <button onClick={downloadChartAsPng} className="ml-4 bg-blue-500 text-white px-3 py-1 rounded">
              Download as PNG
            </button>
          </div>
        }
        <div ref={wrapperRef}>
          <svg ref={svgRef}></svg>
          <svg ref={lineChartRef}></svg>
        </div>
      </div>
    );
}

export default DataVisualization;

